.reachme-container {
  color: var(--color-info-light);
  width: 100%;
  padding: 0 40px;
  position: relative;
  padding: 50px 40px 20px 40px;
  background: var(--color-background-2);
}
.reachme-container .row {
  display: flex !important;
  justify-content: flex-start;
  margin: auto;
}
.email-me input {
  width: 100%;
  margin-bottom: 10px;
  height: 55px;
  background: var(--color-card2);
  border: none;
  border-radius: 6px;
  text-indent: 20px;
  color: #ffffff;
}
.email-me textarea {
  width: 100%;
  background: var(--color-card2);
  border-radius: 5px;
  text-indent: 20px;
  color: #ffffff;
  padding-top: 20px;
}
.hire-me-title h2 {
  font-size: 1.2rem;
}
.hire-btn {
  height: 50px;
  width: 150px;
  background: var(--color-button-hover);
  border-radius: 10px;
  font-size: 15px;
  margin-top: 30px;
}
.hire-btn:hover {
  color: #000000;
}
.reachme-title {
  width: 100%;
}
.reachme-title2 {
  text-align: center;
  margin: 30px auto;
  max-width: 450px;
}
.reachme-title2 h2 {
  font-size: 14px;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 10px;
  color: var(--color-title);
  letter-spacing: 3px;
  color: var(--color-title);
  font-weight: 600;
}
.reachme-title2 h3 {
  text-transform: capitalize;
  font-size: 2.5rem;
  color: white;
}
.icon-name {
  margin-top: 0px;
  font-size: 1.3rem;
}
.reachme-title .row {
  margin-top: 20px;
}
.reachme-title h2 {
  font-size: 1.8rem;
}
.reachme-title p {
  font-size: 15px;
  line-height: 30px;
}
.contact-info {
  display: flex;
  cursor: pointer;
  align-items: center;
  margin-bottom: 10px;
  margin-right: 10px;
  border-radius: 10px;
  cursor: pointer;
  padding: 10px 0px 0px 10px;
  /* border: 1px solid; */
}

.contact-details {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-right: 20px;
  /* border: 1px solid; */
  text-align: left;
}
.contact-mi {
  margin-left: 10px;
  margin-top: 10px;
}
.email-me {
  display: flex;
  justify-content: center;
  align-items: center;
}
.contact-mi p {
  font-size: 14px;
}
.contact-mi h4 {
  margin-bottom: 0;
  padding-bottom: 0;
  /* font-size: 1.3rem; */
}
.contact-info i {
  margin-bottom: 10px;
  font-size: 30px;
  border-radius: 50%;
  display: flex;
  color: var(--color-button-hover);
  justify-content: center;
  align-items: center;
  height: 80px;
  width: 80px;
  border-radius: 150px;
  margin-right: 20px;
  margin-top: 5px;
}
.contact-info:nth-child(1) i {
  color: #f06a6a !important;
  background: #fcdcdc;
}
.contact-info:nth-child(2) i {
  color: #6ecc21 !important;
  background: #ddf5e6;
}
.contact-info:nth-child(3) i {
  color: #8b71e9 !important;
  background: #ddd6fa;
}
.contact-form {
  padding: 5px;
  border-radius: 5px;
}

@media only screen and (max-width: 689px) {
  .reachme-title .row {
    justify-content: center;
  }
  .reachme-title h2 {
    text-align: center;
  }
  .reachme-title p {
    text-align: center;
  }
  .contact-info i {
    font-size: 30px;
    height: 50px;
    width: 50px;
  }
  .cont-email > :nth-child(3) {
    margin-left: 36px;
  }
  .contact-mi {
    font-size: 1.3rem;
    text-align: center;
  }
  .contact-mi p {
    font-size: 0.8rem;
  }
}
@media only screen and (max-width: 550px) {
  .email-me {
    padding: 0px !important;
  }
  .reachme-container {
    padding: 10px;
  }
  .reachme-title2 h2 {
    font-size: 0.8rem;
  }
  .contact-info i {
    font-size: 25px;
    height: 40px;
    width: 40px;
  }
  .icon-name {
    margin-top: -10px;
    font-size: 1rem;
  }
}

/* light mode presets */
.light .email-me input {
  color: #000000;
}
.light .email-me textarea {
  color: #000000;
}
.light .contact-form {
  box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px,
    rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
  background: var(--color-background-2);
}
.light .reachme-container {
  background-color: white;
}
.light .contact-info i {
  /* border: 1px solid; */
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}
