#projects {
  /* margin-top: 50px; */
  padding-top: 50px;
  /* border: 1px solid; */
  /* padding-bottom: 30px; */
}
.mywork {
  color: var(--color-info-light);
  width: 100%;
  padding: 0 60px 0 60px;
  overflow: hidden;
}
.mywork-title {
  margin-bottom: 50px;
  text-align: center;
}
.mywork-title h2 {
  font-size: 14px;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 10px;
  color: var(--color-title);
  letter-spacing: 3px;
  color: var(--color-title);
  font-weight: 600;
}
.project-links {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}
.mywork-title h3 {
  text-transform: capitalize;
  font-size: 2.5rem;
}
.project {
  overflow: hidden !important;
  min-width: 300px;
  background: var(--color-card2);
  border: 1px solid rgb(255 255 255 / 0.1);
  border-radius: 17px;
  padding: 20px;
  margin-bottom: 20px;
}
.project-img {
  overflow: hidden;
  display: block;
  position: relative;
}
.project-row {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 15px;
  /* border: 1px solid; */
}
.work-img {
  width: 100%;
  display: inline-block !important;
  border-radius: 15px !important;
  padding: 10px;
  transition: 0.5s;
  height: 220px;
  cursor: pointer;
  transform: scale(0.9) !important;
  /* border: 1px dotted gray; */
  box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px,
    rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
}
.project:hover .work-img {
  transform: scale(1) !important;
  box-shadow: none;
  /* box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px; */
}

.date-posted {
  display: flex !important;
  align-items: center !important;
  padding: 10px 0;
  justify-content: space-between;
  padding-left: 16px;
  padding-right: 20px;
}

.work-details {
  padding: 0px 20px 20px 20px;
  position: relative;
}
.work-details h2 {
  font-size: 1.5rem;
  margin-bottom: 20px;
  max-height: 100px;
  font-weight: 700;
  text-align: left;
}
.work-details p {
  font-size: 13px;
  word-break: break-all;
  font-weight: 300 !important;
  line-height: 1.7rem;
  white-space: normal;
  text-align: left;
}
.admin {
  font-family: "SF Mono", "Fira Code", "Fira Mono", "Roboto Mono", monospace;
}
.who-post {
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--color-title) !important;
}
.who-post p {
  margin: 0 !important;
  margin-left: 5px !important;
  text-transform: uppercase;
  color: var(--color-button-hover) !important;
  font-size: 14px !important;
  background: var(--color-button);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 600 !important;
}
.work-info {
  font-size: 12px;
}

.project .learnmore {
  background: none;
  padding: 10px;
  display: flex;
  font-weight: 100;
  letter-spacing: 1px;
  justify-content: center;
  align-items: center;
  font-size: 13px;
  text-transform: uppercase;
  font-weight: 300;
  color: #909faf;
  cursor: pointer;
  color: var(--color-button);
  border-radius: 4px;
}
.learnmore i {
  margin-bottom: 2.1px;
}
.learnmore:hover {
  color: black;
  background-color: var(--color-button-hover);
}
.project:hover {
  border-bottom: 4px solid var(--color-button);
}

.light .project {
  background: var(--color-card-background);
  box-shadow: var(--card-box-shadow);
}

/* carousel controls */
.owl-carousel .owl-nav button.owl-prev,
.owl-carousel .owl-nav button.owl-next {
  z-index: 1;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  transform: translatey(-50%);
}
.owl-carousel .nav-btn {
  font-size: 28px;
  transition: all 0.5s;
  display: inline-flex;
  cursor: pointer;
}
.owl-carousel .owl-nav button.owl-prev {
  left: 0;
  margin-left: -10px;
}
.owl-carousel .owl-nav button.owl-next {
  right: 0;
  margin-right: -10px;
}
.owl-carousel .owl-nav {
  margin: 0;
}
.btn-wrap {
  text-align: center;
  width: 100%;
}
.owl-theme .owl-nav .disabled,
button.disabled {
  opacity: 0.6;
}

@media only screen and (max-width: 768px) {
  .mywork .mywork-title {
    text-align: center;
  }
  .mywork .mywork-title h3 {
    font-size: 2rem;
  }
  .work-togather-text {
    padding-bottom: 20px;
  }
  .sec-btn {
    font-size: 13px;
  }
  .work-togather-form {
    padding-left: 0px;
  }
  .project .learnmore {
    font-size: 12px;
  }
  .owl-item {
    display: flex !important;
    justify-content: center;
  }
  .owl-dots {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .work-img {
    padding: 5px;
    /* width: 100%; */
    height: fit-content;
  }
}

@media only screen and (max-width: 425px) {
  .project {
    /* width: 200px; */
    width: 90%;
    margin: auto;
  }
  .mywork-title {
    text-align: center;
  }
  .mywork-title h3 {
    font-size: 1.8rem !important;
    word-wrap: wrap;
  }
  .mywork {
    padding: 0 0 0 0px;
  }
  .project .learnmore {
    font-size: 12px;
  }
  .work-img {
    padding: 5px;
    /* width: 100%; */
    height: fit-content;
  }
  .work-details {
    padding: 0 10px 10px 10px;
  }
}
@media only screen and (max-width: 360px) {
  .project {
    padding: 10px;
    min-width: 280px !important;
  }
}
.light #projects {
  background-color: white;
}
.light .project-card {
  box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px,
    rgb(209, 213, 219) 0px 0px 0px 1px inset;
}
.light .work-details h2 {
  color: black !important;
  font-family: "poppins", sans-serif !important;
}
.light .who-post p {
  color: #ff5652 !important;
}
.light .project .learnmore {
  color: rgb(5, 5, 5) !important;
}
.light .project .learnmore:hover {
  color: rgb(5, 5, 5) !important;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  color: var(--color-button-hover);
}
