@import url("https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@300;400;500;600;700;800&display=swap");
.aboutMe-container {
  color: var(--color-info-light);
  width: 100%;
  position: relative;
  /* margin-top: 50px; */
  padding: 70px 40px 50px 50px;
  background: var(--color-background-2);
  /* border: 1px solid; */
}

.abouMe-row {
  display: flex !important;
  align-items: center !important;
  justify-content: space-around;
  flex-wrap: wrap-reverse !important;
 
  margin: auto;
}
.abouMe-row img {
  width: 40%;
  margin-left: 150px;
  border-radius: "20px";
}
.abouMe-row .title h2 {
  font-size: 22px;
  text-transform: uppercase;
  letter-spacing: 3px;
  margin-bottom: 10px;
}

.abouMe-row h3 {
  font-size: 2rem;
  line-height: 40px;
  text-transform: uppercase;
  width: 490px;
  font-weight: 500;
  margin-bottom: 10px;
}

.about-description {
  position: relative;
  text-align: left;
}
.pt-2 {
  font-family: "Source Code Pro", monospace;
}

.about-description p {
  font-size: 1.3rem;
  font-weight: 400 !important;
  margin-bottom: 20px;
  line-height: 2rem;
  font-weight: 300;
  text-align: left;
  color: var(--color-white);
  font-family: "Rubik", sans-serif !important;
}

.download-cv {
  height: 60px;
  width: 180px;
  outline: none;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 500;
  font-size: 13px;
  border: none;
  color: var(--color-background-2);
  border-radius: 5px;
  border: 1px solid var(--color-button);
  background: var(--color-button-hover);
  margin: 20px auto;
}
*.unselectable {
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none !important;
}
.ck-editor__top {
  display: none !important;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none !important;
}
.ck-editor__top p,
strong,
i {
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none !important;
}
.ck-content {
  user-select: none !important;
}
.ck-content i {
  color: #006eff !important;
  pointer-events: none;
}
.ck.ck-editor__main > .ck-editor__editable {
  background: none !important;
  border: none !important;
  pointer-events: none !important;
}

/* Responsiveness */
@media only screen and (max-width: 865px) {
  .aboutMe-container {
    padding: 30px 10px;
    overflow: hidden;
  }
  .abouMe-row .title h3 {
    font-size: 1.3rem;
    margin-bottom: 20px;
    line-height: 25px;
  }
  .abouMe-row .title h2 {
    font-size: 0.8rem;
  }
  .about-info {
    font-family: "Source Code Pro", monospace;
  }
  .abouMe-row {
    padding: 10px;
  }
  .abouMe-row {
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
  }
  .about-img img {
    /* margin-right: 0 !important; */
    width: 100%;
    margin-left: 20px;
  }
  .lang i {
    height: 40px;
    width: 40px;
    font-size: 20px;
  }
  .about-description p {
    font-size: 13px;
    text-align: center;
  }
  .download-cv {
    margin-top: 10px !important;
    width: 130px;
    font-size: 12px;
    height: 45px;
  }
}

@media only screen and (max-width: 768px) {
  .aboutMe-container {
    padding: 30px 0px;
    overflow: hidden;
  }
  .abouMe-row .title h2 {
    font-size: 1.5rem;
    margin-bottom: 20px;
    line-height: 25px;
  }
  .about-img {
   
    margin: 20px auto;
   
    width: 100%;
  }
  .about-img img {
    margin: 10px auto;
  }
  .lang i {
    height: 40px;
    width: 40px;
    font-size: 20px;
  }
  .about-description p {
    font-size: 14px;
    text-align: center;
  }
  .download-cv {
    margin-top: 10px !important;
    width: 140px;
    font-size: 12px !important;
    height: 45px;
  }
}

@media only screen and (max-width: 767px) {
  .lang i {
    width: 30px;
    height: 30px;
  }
  .about-img img {
    width: 40%;
    margin: 10px auto;
  }
  .about-description p {
    text-align: center;
  }
}

@media only screen and (max-width: 425px) {
  .about-img {
    text-align: center;
  }
  .about-img img {
    margin-right: 0;
    width: 75%;
    margin: 10px auto;
  }
}

@media only screen and (max-width: 425px) {
  .abouMe-row {
    padding: 10px;
  }
}

/* light theme */
.light .lang i {
  background: rgb(214, 214, 214);
}

.light .abouMe-row h3 {
  color: rgb(41, 41, 41);
}
.light .abouMe-row h2 {
  color: #ff5652 !important;
}
.light .download-cv {
  background: var(--btn-background);
}
.light .download-cv:hover {
  background: var(--color-button);
}
