.progress-container {
    color: var(--color-info-light);
    padding: 50px 50px 70px 50px;
    background: var(--color-background-2);
  }
  .progress-row2 {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
  }
  .fullwidth {
    background-color: rgb(114 226 174/0.2);
    height: 13px;
    width: 100%;
    bottom: 0;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    border-radius: 10px;
    position: relative;
  }
  .progress-bar2 {
    height: 45px;
    width: 100%;
    position: relative;
    border-radius: 5px;
    margin-bottom: 30px;
  }
  .progress-container .skill-info {
    display: flex;
    padding: 10px 0px;
    justify-content: space-between;
  }
  .skill-bar {
    position: absolute;
    left: 0;
    background: var(--color-button-hover);
    height: 13px;
    bottom: 0;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    border-radius: 10px;
  }
  .skill-detail {
    display: flex !important;
    flex-direction: row;
  }
  .skill-name {
    margin-left: 10px;
    text-transform: uppercase;
    font-size: 12px !important;
    letter-spacing: 1px;
  }
  .skill-info {
    padding-top: 10px;
  }
  .progress-title {
    margin-bottom: 50px;
    text-align: center;
  }
  .progress {
    width: 100%;
    height: 50px;
  }
  .progress-title h2 {
    font-size: 14px;
    font-weight: 300;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin-bottom: 10px;
    color: var(--color-title);
    letter-spacing: 3px;
    color: var(--color-title);
    font-weight: 600;
  }
  .progress-title h3 {
    text-transform: capitalize;
    font-size: 2.5rem;
  }
  
  .fa-html5 {
    color: #f35f29;
  }
  .fa-css3 {
    color: #00aeff;
  }
  .fa-js-square {
    color: #e9b50a;
  }
  .fa-react {
    color: #00aeff;
  }
  .fa-facebook {
    color: #5656be;
  }
  .fa-python {
    color: #57a4d1;
  }
  
  .fa-database {
    color: #0cf;
  }
  .progress-container .bxl-django {
    color: green;
    font-size: 3.7rem !important;
    margin-top: 3px;
  }
  .bxl-firebase {
    color: yellow;
  }
  .bxs-component {
    color: rgb(115, 240, 84);
  }
  .bx-code-block {
    color: rgb(255, 0, 179);
  }
  .mylang {
    background: var(--color-card2);
    margin-right: 15px;
    display: flex;
    justify-content: center;
    border-radius: 10px;
    cursor: pointer;
    padding: 20px 0;
    align-items: center;
    border: 1px solid rgba(146, 146, 165, 0.1);
  }
  .mylang h3 {
    color: rgb(255, 255, 255);
    font-size: 1.2rem;
  }
  .mylang p {
    border: 1px solid rgb(37, 43, 88);
    border-radius: 10px;
    padding: 0 20px;
  }
  
  .lang {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    justify-content: space-between !important;
    flex-wrap: wrap;
    width: 100%;
  }
  .lang-img {
    font-size: 3.7rem;
    width: 70px;
    margin-bottom: 20px;
    cursor: pointer;
    height: 70px;
    margin-top: 5px;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background: rgb(23, 32, 61);
    /* border: 1px solid red; */
    /* box-shadow: 0px 3px 5px rgb(16 70 197 / 20%); */
  }
  .lang-info {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: center;
  }
  .lang-info img {
    width: 48px;
    height: 48px;
  }
  .lang-info p {
    font-size: 13px;
  }
  /* responsive */
  @media only screen and (max-width: 768px) {
    .progress-title {
      text-align: center;
    }
    .progress-title h3 {
      font-size: 1.8rem!important;
    }
  }
  
  @media only screen and (max-width: 425px) {
    .progress-container {
      padding: 50px 20px;
    }
  }
  /*light theme  */
  .light .skill-bar {
    background: var(--btn-background);
  }
  .light .mylang h3 {
    color: #000;
  }
  .light .lang-img {
    background: rgb(240, 239, 239);
  }
  .light .mylang p {
    border: 1px solid rgb(215, 217, 231);
    line-height: normal !important;
  }
  .light .progress-container {
    background-color: #e4e5f1 !important;
  }
  .light .lang-img{
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
  }